export const bool = (attr) => {
  if (attr == null) return null;

  const attr2 = attr.trim();
  if (attr2 === 'false' || attr2 === 'null' || attr2 === 'undefined' || attr2 === '0') {
    return false;
  }

  return true;
};

bool.stringify = b => `${!!b}`;

export default bool;



